<template>
    <div class="loading"  :style="{backgroundImage:`url(${img})`}">
        <img src="../../static/img/logo1.png" alt="">
        <h4>为您提供稳定在线安全监测情况</h4>
    </div>
</template>

<script>
export default {
    data(){
        return{
            img:require('../../static/img/back1.png')
        }
    }
};
</script>

<style lang="scss" rel="stylesheet/scss">
@import "../assets/hcsy.scss";
.loading {
    height: 100%;
    width: 100%;
    position: absolute;
    overflow: hidden;
    background-image: url("../../static/img/back1.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    > img {
        width: rem(360);
        margin: rem(220) auto 0;
        display: block;
        height: auto;
    }
    h4{
        font-size: rem(45);
        margin: rem(230) auto 0;
        text-align: center;
        color: white;
    }
}
</style>



